import React, { useEffect, useState } from "react";
import { Route, Router, Routes, Navigate, useLocation } from "react-router-dom";
import "./App.scss";
import { Inbox } from "./components/inbox/Inbox";
import { Calendar } from "./components/calendar/Calendar";
import { TicklerFile } from "./components/ticklerFile/TicklerFile";
import { ActionableList } from "./components/ActionableList/ActionableList";
import { SomdayDumper } from "./components/SomedayDumper/SomdayDumper";
import { Header } from "./components/headers/Header";
import { DayView } from "./components/calendar/dayView/DayView";
import { LoginSignUp } from "./components/Login/LoginSignUp";

function App() {
  const [userId, setUserId] = useState(JSON.parse(localStorage.getItem("user_id")));
  const location = useLocation();

  return (
    <div className="App">
      {location.pathname !== "/GTD_project/login" && <Header />}

      <Routes>
        {userId ? (
          <>
            <Route path="/GTD_project/Inbox" element={<Inbox />} />
            <Route path="/GTD_project/Calendar" element={<Calendar />} />
            <Route path="/GTD_project/Calendar/DayView" element={<DayView />} />
            <Route path="/GTD_project/Tickler" element={<TicklerFile />} />
            <Route path="/GTD_project/Actionables" element={<ActionableList />} />
            <Route path="/GTD_project/Dumper" element={<SomdayDumper />} />
            <Route path="/GTD_project" element={<Navigate to="/GTD_project/Inbox" />} />
          </>
        ) : (
          <>{location.pathname !== "/GTD_project/login" && <Route path="/GTD_project/*" element={<Navigate to="/GTD_project/login" />} />}</>
        )}

        <Route path="/GTD_project/login" element={<LoginSignUp setUserId={setUserId} />} />
      </Routes>
    </div>
  );
}

export default App;
